                        <br>Quais aes municipais so necessrias para viabilizar a elaborao
                         dos Planos de Mobilidade Urbana (PMU)? As restries oramentrias e a
                         quantidade de servidores conseguiro gerenciar este processo? A
                         obrigatoriedade e o tempo disponvel para a implementao so
                         oportunidades ou desafio?<br>
                        Nesse contexto, a palestra apresenta a experincia da Equipe da UFSC na
                         construo e execuo da metodologia do Projeto PlanMob, que auxiliou
                         na estruturao dos planos de mobilidade em oito municpios de pequeno
                         porte simultaneamente. Enaltecendo a necessidade de vinculao entre
                         polticas de transporte e mobilidade destes municpios do nordeste
                         catarinense em decorrncia dos fluxos resultantes pela interdependncia
                         da infraestrutura viria disponvel. A percepo das causas e
                         consequncias  construo de diretrizes de desenvolvimento e de
                         planejamento urbano de forma conjunta.<br>
                        A sistematizao das atividades foi dividida em oito etapas. A primeira
                         enfatiza a estruturao poltico-executiva do Municpio necessrias;
                         as seis etapas subsequentes elucidam o desenvolvimento do Plano de
                         Mobilidade Urbana; e a ltima etapa indica  elaborao da Carta de
                         Intenes do Plano de Mobilidade Regional.<br>
                        A metodologia proposta pela Equipe UFSC oportunizou a realizao de
                         palestras e capacitaes tcnicas para o uso de ferramentas e tcnicas
                         de planejamento e monitoramento da mobilidade urbana. Alm do estmulo
                         da participao direta da populao e de seus representantes na
                         formulao, execuo e acompanhamento das atividades para ampliar a
                         consolidao do PMU.<br>
                        Alguns desafios para a equipe UFSC foram o tempo requerido para a
                         execuo do projeto; o desenvolvimento simultneos das etapas nos
                         municpios; e a participao como agentes executores de seus
                         prprios Planos de Mobilidade.<br>
                        O objetivo da conduo do processo foi viabilizar a elaborao do
                         Plano de Mobilidade Urbana, e principalmente, permitir que a
                         reviso, continuidade e integrao com o planejamento urbano seja
                         realizada pelos municpios com autonomia em qualquer poca.<br>
