                         planejamento do sistema virio e trfego. O preceito de
                         identificar o uso e a ocupao do solo, preocupando-se com o meio
                         ambiente e patrimnio, conduziu a anlise dos deslocamentos no
                         motorizados como atividade cotidiana necessria e salutar ao ser humano.
